import Image from "next/image";

const AuthPage = () => {
  return (
    <div className="min-h-screen flex items-center justify-center bg-yellow-100 px-4 py-12">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4 w-full max-w-7xl bg-white rounded-xl shadow-xl">
        <div className="p-8 sm:p-12 min-h-[750px] flex flex-col justify-center">
          <h2 className="text-3xl font-bold text-blue-900 mb-2">Sign In</h2>
          <p className="text-sm text-gray-700">Welcome brok!!</p>

          <form action="" className="space-y-4">
            <div>
              <label
                htmlFor="email"
                className="block text-sm font-medium text-gray-700"
              >
                Email
              </label>
              <input
                type="email"
                value={""}
                placeholder="example.com"
                className="w-full mt-1 py-2 px-3 border border-gray-300 bg-white rounded-md text-sm shadow-sm placeholder:text-gray-400 focus:outline-none focus:border-sky-500 focus:ring-1 focus:ring-sky-500"
              />
            </div>
            <div>
              <label
                htmlFor="password"
                className="block text-sm font-medium text-gray-700"
              >
                Password
              </label>
              <input
                type="text"
                value={""}
                placeholder="example.com"
                className="w-full mt-1 py-2 px-3 border border-gray-300 bg-white rounded-md text-sm shadow-sm placeholder:text-gray-400 focus:outline-none focus:border-sky-500 focus:ring-1 focus:ring-sky-500"
              />
            </div>
          </form>
        </div>
        <div className="hidden md:block bg-gray-200 relative min-h-[750px] w-full">
          <Image
            src="/images/auth-img.png"
            alt="auth"
            fill
            priority
            className="object-cover"
          />
        </div>
      </div>
    </div>
  );
};

export default AuthPage;
